 };\r\n        },\r\n        created() {\r\n            let root = document.documentElement;\r\n            root.style.setProperty('--time-animation', \"load \" + this.speed + \"s infinite alternate ease-in-out\");\r\n            root.style.setProperty('--color-bar',this.objectbg);\r\n            var c;\r\n            if(/^#([A-Fa-f0-9]{3}){1,2}$/.test(this.bg)){\r\n                c= this.bg.substring(1).split('');\r\n                if(c.length== 3){\r\n                    c= [c[0], c[0], c[1], c[1], c[2], c[2]];\r\n                }\r\n                c= '0x'+c.join('');\r\n                this.bgcolors= 'rgba('+[(c>>16)&255, (c>>8)&255, c&255].join(',')+',' + this.opacity/100 +')';\r\n            }\r\n        }   \r\n        \r\n    }\r\n</script>\r\n\r\n\r\n<style>\r\n    :root{\r\n        --time-animation: load .7s infinite alternate ease-in-out;\r\n        --color-bar: #ffbb0f;\r\n    }\r\n #overlay-bar{\r\n        background-color: rgba(52,58,64,1);\r\n        height: 100%;\r\n        width: 100%;\r\n        position: fixed;\r\n        top: 0;\r\n        left: 0;\r\n        right: 0;\r\n        bottom: 0;\r\n        z-index:1000;\r\n    }\r\n.loader-bar{\r\n    width: 250px;\r\n  height: 50px;\r\n  line-height: 50px;\r\n  text-align: center;\r\n  position: absolute;\r\n  top: 50%;\r\n  left: 50%;\r\n  transform: translate(-50%,-50%);\r\n   text-transform: uppercase;\r\n  font-weight: 900;\r\n  color: #ce4233;\r\n  letter-spacing: 0.2em;\r\n  }\r\n\r\n  .loader-bar:before, .loader-bar:after {\r\n    content: \"\";\r\n    display: block;\r\n    width: 15px;\r\n    height: 15px;\r\n    background: var(--color-bar);\r\n    position: absolute;\r\n    animation: var(--time-animation);\r\n  }\r\n  \r\n  .loader-bar:before {\r\n    top: 0;\r\n  }\r\n  \r\n  .loader-bar:after {\r\n    bottom: 0;\r\n  }\r\n\r\n  @keyframes load {\r\n  0% { left: 0; height: 30px; width: 15px }\r\n  50% { height: 8px; width: 40px }\r\n  100% { left: 235px; height: 30px; width: 15px}\r\n